import React, { Component } from "react";
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem } from "reactstrap";
import { Link } from "react-router-dom";

import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Label, Col, Row } from 'reactstrap';
import { Control, LocalForm, Errors } from "react-redux-form";
import { Loading } from "./LoadingComponent";
import { baseUrl } from "../shared/baseUrl";
import { FadeTransform, Fade, Stagger } from "react-animation-components";

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => !(val) || (val.length >= len);

class CommentForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false
        }
    }

    toggle = () => {
        this.setState({showModal: !this.state.showModal});
    };

    handleSubmit = (values) => {

        this.toggle();
        this.props.postComment(this.props.dishId, values.rating, values.yourName, values.comment);
    }

    render() {
        return (
            <div>
            <Button color="white" onClick={this.toggle}><i className="fa fa-pencil" aria-hidden="true"></i> Submit Comment</Button>
            <Modal isOpen={this.state.showModal}>
                <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                <ModalHeader>Submit Comment</ModalHeader>
                <ModalBody>
                    <Row className="form-group">
                        <Label htmlFor="rating" md={12}>Rating</Label>
                        <Col md={12}>
                            <Control.select model=".rating" id="rating" name="rating"
                                placeholder="Rating" className="form-control"
                                validators={{
                                    required
                                }}
                            >
                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                            </Control.select>
                            <Errors 
                                className="text-danger"
                                model=".rating"
                                show="touched"
                                messages={{
                                    required: "Required",
                                }}
                            />
                        </Col>
                    </Row>
                    <Row className="form-group">
                        <Label htmlFor="yourName" md={12}>Your Name</Label>
                        <Col md={12}>
                            <Control.text model=".yourName" id="yourName" name="yourName"
                                placeholder="Your Name" className="form-control"
                                validators={{
                                    required, minLength: minLength(3), maxLength: maxLength(15)
                                }}
                            />
                            <Errors 
                                className="text-danger"
                                model=".yourName"
                                show="touched"
                                messages={{
                                    required: "Required",
                                    minLength: "Must be greater than 2 characters",
                                    maxLength: "Must be 15 characters or less"
                                }}
                            />
                        </Col>
                    </Row>
                    <Row className="form-group">
                        <Label htmlFor="comment" md={12}>Comment</Label>
                        <Col md={12}>
                            <Control.textarea model=".comment" id="comment" name="comment"
                                placeholder="Comment" className="form-control" rows="6"
                            />
                        </Col>
                    </Row>
                </ModalBody>
                <ModalFooter>
                <Button type="submit" color="primary">Submit</Button>
                </ModalFooter>
                </LocalForm>
            </Modal>
            </div>
        );
    }
}

function RenderComments({comments, postComment, dishId}) {   
        const listItems = comments.map((comment) =>
                <Fade in>
                <ul key={comment.id} className="list-unstyled">
                    <li>{comment.comment}</li>
                    <li>&#45;&#45; {comment.author}&#44; {new Intl.DateTimeFormat('en-US',{year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}</li>
                </ul>
                </Fade>
        );
        return (
            <>
                <Stagger in>
                    {listItems}
                </Stagger>
                <CommentForm dishId={dishId} postComment={postComment} />
            </>
        );
}

function RenderDish({dish}) {
    return (
        <FadeTransform in
                transformProps={{
                    exitTransform: "scale(0.5) translate(-50%)"
                }}>
            <Card>
                <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
                <CardBody>
                    <CardTitle>{dish.name}</CardTitle>
                    <CardText>{dish.description}</CardText>
                </CardBody>
            </Card>
        </FadeTransform>
    )
}

const DishDetail = (props) => {
    if(props.isLoading) {
        return (
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        )
    }
    else if(props.errMess) {
        return (
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        )
    }

    else if(props.dish && props.comments) {
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-12 col-md-5 mt-1">
                        <RenderDish dish = {props.dish} />
                    </div>
                    <div className="col-12 col-md-5 mt-1">
                        <h4>Comments</h4>
                        <RenderComments comments = {props.comments} postComment={props.postComment}
                            dishId={props.dish.id} />
                    </div>
                </div>
            </div>
        );
    }
    else {
        return <div></div>;
    }
}

export default DishDetail;