import * as ActionTypes from "./ActionTypes";

export const InitialFeedback = (state={
        errMess: null,
        feedbacks: []
    }, action) => {
    switch(action.type) {
        case ActionTypes.ADD_FEEDBACK: 
            var feedback = action.payload;
            return {...state, feedbacks: state.feedbacks.concat(feedback)};

        case ActionTypes.FEEDBACK_FAILED:
            return {...state, errMess: action.payload};

        case ActionTypes.ADD_FEEDBACKS:
            console.log("in ADD_FEEDBACKS", action.payload);
            return{...state, errMess:null, feedbacks: action.payload};

        default:
            return state;
    }
}